<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-row
      class="xs12 md12"
    >
      <v-col class="d-flex justify-between align-center mb-3">
        <v-btn
          v-for="mail_template in api"
          :key="mail_template.id"
          color="primary"
          @click="switchTemplate(mail_template.id)"
        >
          {{ mail_template.template }}
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="5"
        md="5"
      >
        <base-material-card
          color="primary"
          icon="mdi-mail"
          title="Mail"
          class="px-5 py-3"
        >
          <v-col>
            <v-form
              ref="form"
              lazy-validation
            >
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <v-text-field
                    v-model="selected.image"
                    label="Link to image"
                    :rules="validate.text"
                    required
                  />
                </v-col>
              </v-row>
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <v-text-field
                    v-model="selected.subject"
                    label="Email subject"
                    :rules="validate.text"
                    required
                  />
                </v-col>
              </v-row>
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <span>Header</span>
                  <vue-editor v-model="selected.header" />
                </v-col>
              </v-row>
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <span>Body</span>
                  <vue-editor v-model="selected.body" />
                </v-col>
              </v-row>
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <span>Footer</span>
                  <vue-editor
                    v-model="selected.footer"
                    required
                  />
                </v-col>
              </v-row>
              <v-row
                class="xs12 md12"
              >
                <v-col>
                  <v-btn
                    color="primary"
                    @click="submit"
                  >
                    Update
                  </v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-col>
        </base-material-card>
      </v-col>
      <v-col
        cols="7"
        md="7"
      >
        <base-material-card
          color="primary"
          icon="mdi-file-find"
          title="Preview"
          class="px-5 py-3"
        >
          <mail-template
            :image="selected.image"
            :header="selected.header"
            :body="selected.body"
            :footer="selected.footer"
          />
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { VueEditor } from 'vue2-editor'
  import MailTemplate from '@/templates/mail/MailTemplate'
  import validation from '@/utils/validation'

  export default {
    name: 'Mail',

    components: {
      VueEditor, MailTemplate,
    },
    data: () => ({
      validate: validation,
      selected: {},
      api: {},
    }),
    mounted: function () {
      this.$http.get(`/portal/template/mail`)
        .then(
          (response) => {
            this.api = response.data
            this.selected = this.api[0]
          }
        )
    },
    methods: {
      submit: function () {
        if (!this.$refs.form.validate()) {
          return
        }

        this.$http.post(
          `/portal/template/mail/${this.selected.id}`,
          JSON.stringify({
            subject: this.selected.subject,
            header: this.selected.header,
            body: this.selected.body,
            footer: this.selected.footer,
            image: this.selected.image,
          })
        )
          .then((response) => {
            this.$toasted.global.success(response.data.message)
            this.$router.push({ name: 'mail' })
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
      switchTemplate: function (templateId) {
        this.api.forEach(function (value) {
          if (value.id === templateId) {
            this.selected = value
          }
        }, this)
      },
    },
  }
</script>

